@import url(https://fonts.googleapis.com/css?family=Roboto:400,700);
/* @import url('https://fonts.googleapis.com/css?family=Nanum+Gothic+Coding:400,700|Roboto:400,700'); */

html {
  position: relative;
  min-height: 100%;
}

body {
  margin-top: 0;
  margin-bottom: 60px; /* Margin bottom by footer height */
  padding: 0;
  /* font-family: 'Nanum Gothic Coding', monospace, 'Roboto', sans-serif;   */
  font-family: 'Roboto',  sans-serif;  
}

.card {
  padding: 5px;
  margin: 5px;
}
.icon {
  padding: 0 2px;  
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  background-color: #f5f5f5;
  padding: 1% 0;
  /* margin-top: 10%; */
}

/* 레이아웃 전반 스타일*/
.title {
  /* text-align: center; */
  font-weight: bold;
  font-size: 20pt;
  color: #576574;
}

.container {
  width: auto;
  max-width: 960px;
  padding: 0 15px;
}

.panel-borderless {
  border: 0;
  box-shadow: none;
}
.pointer {
  cursor: pointer;
}
.name {
  font-size: 1.5em;
  font-weight: 400;
}

/* 사진의 크기 제한 */
.photoWidth {
  max-width: 80px;
  min-width: 80px;
}

/* 모달 박스를 위한 스타일*/
.modal {
  display: block;
  position: fixed;
  z-index: 999;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
}
.form {
  background-color: white;
  margin: 80px auto;
  max-width: 400px;
  min-width: 200px;
  font: 13px "verdana";
  padding: 10px 10px 10px 10px;
}
.loading {
  background-color: white;
  margin: 250px auto;
  max-width: 400px;
  min-width: 200px;
  font: 13px "verdana";
  padding: 10px 10px 10px 10px;
}
.form div {
  padding: 0;
  display: block;
  margin: 5px 0 5px 0;
}
.form label {
  text-align: left;
  margin: 0 0 3px 0;
  padding: 0px;
  display: block;
  font-weight: bold;
}
.form input,
textarea,
select {
  box-sizing: border-box;
  border: 1px solid #bebebe;
  padding: 7px;
  margin: 0px;
  outline: none;
}
.form .long {
  width: 100%;
}
.form .button {
  background: #2b798d;
  padding: 8px 15px 8px 15px;
  border: none;
  color: #fff;
}
.form .button:hover {
  background: #4691a4;
}
.form .heading {
  background: #33a17f;
  font-weight: 300;
  text-align: left;
  padding: 20px;
  color: #fff;
  margin: 5px 0 30px 0;
  padding: 10px;
  min-width: 200px;
  max-width: 400px;
}

/* 둥근 버튼을 위한 스타일*/
.btn-circle {
  width: 30px;
  height: 30px;
  padding: 6px 0px;
  border-radius: 15px;
  text-align: center;
  font-size: 12px;
  line-height: 1.42857;
}

.container {
  width: auto;
  max-width: 960px;
  padding-bottom: 10px;
}

.nav-item {
  padding: 0 10px;
}

.main {
  padding: 40px 0;
  margin: 40px 0;
  background-color: aquamarine;
  color: white; 
  text-shadow: 0 1px 0 #ccc, 
               0 2px 0 #c9c9c9,
               0 3px 0 #bbb,
               0 4px 0 #b9b9b9,
               0 5px 0 #aaa,
               0 6px 1px rgba(0,0,0,.1),
               0 0 5px rgba(0,0,0,.1),
               0 1px 3px rgba(0,0,0,.3),
               0 3px 5px rgba(0,0,0,.2),
               0 5px 10px rgba(0,0,0,.25),
               0 10px 10px rgba(0,0,0,.2),
               0 20px 20px rgba(0,0,0,.15); 

}

.service { 
  font-size: 3rem;
  padding: 3px 0;
}
.avatar {
  vertical-align: middle;
  width: 320px;
  height: 320px;
  border-radius: 50%;
}
.avatar.size-1 {
  width: 40px;
  height: 40px;
}
.avatar.size-2 {
  width: 70px;
  height: 70px;
}
.avatar.size-3 {
  width: 90px;
  height: 90px;
}
.avatar.size-4 {
  width: 110px;
  height: 110px;
}

.input-group {  
  padding: 10px 0;
  
} 

.input {
  width: 80%; 
  height: 2rem;
}
